import { getPublicPackages } from './actions';
import Link from 'next/link';
import Image from 'next/image';

import amazoniaImg from '../../../public/imgs/amazonia.jpg';
import salvadorImg from '../../../public/imgs/salvador.jpg';
import rioDeJaneiroImg from '../../../public/imgs/rio_de_janeiro.jpg';

type Package = {
  id: string;
  destino: string;
  origem: string;
  preco: number;
  descricao: string | null;
  imagem_url: string | null;
  data_ida: string;
};

const localImages = [rioDeJaneiroImg, salvadorImg, amazoniaImg];

export default async function PackagesPage() {
  const { packages, error } = await getPublicPackages();

  if (error || !packages) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-red-500">Ocorreu um Erro</h1>
          <p className="text-gray-600 mt-2">Não foi possível carregar os pacotes. Verifique o log do servidor para detalhes.</p>
          <Link href="/home" className="mt-6 inline-block py-2 px-5 bg-green-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-600 transition-colors">
            Voltar para a Home
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <header className="bg-white/90 backdrop-blur-md shadow-sm sticky top-0 z-40">
        <nav className="container mx-auto px-6 py-4 flex justify-between items-center">
          <Link href="/home" className="flex items-center gap-3">
            <img src="/imgs/travel_1000dp_FFF_FILL0_wght400_GRAD0_opsz48.svg" alt="Jacarelli Viagens Logo" className="h-8 w-8 bg-green-500 rounded-full p-1" />
            <span className="text-2xl font-bold text-green-500">Jacarelli Viagens</span>
          </Link>
          <Link href="/home" className="py-2 px-5 bg-gray-100 text-gray-700 font-semibold rounded-lg hover:bg-gray-200 transition-colors">
            Voltar
          </Link>
        </nav>
      </header>

      <main className="container mx-auto px-6 py-12">
        <div className="text-center mb-16 animate-fade-in-down">
          <h1 className="text-5xl md:text-6xl font-extrabold text-gray-900 mb-4">Pacotes Imperdíveis</h1>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
            Explore os melhores destinos do Brasil. Preparamos tudo para que sua única preocupação seja aproveitar.
          </p>
        </div>

        {packages.length === 0 ? (
          <div className="text-center py-16">
            <h2 className="text-2xl font-bold text-gray-700">Nenhum Pacote Disponível</h2>
            <p className="text-gray-500 mt-2">No momento, não temos pacotes de viagem ativos. Volte em breve!</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {packages.map((pkg, index) => {
              const formattedDate = new Date(pkg.data_ida).toLocaleDateString('pt-BR', {
                day: '2-digit', month: 'long', year: 'numeric',
              });

              return (
                // CORREÇÃO DA SOMBRA: A sombra, a translação e o will-change ficam no contêiner pai.
                <Link key={pkg.id} href={`/packages/${pkg.id}`} className="block group transform hover:-translate-y-2 transition-all duration-300 will-change-transform">
                  {/* O card em si tem o bg, a sombra e cantos arredondados, mas NÂO o overflow-hidden */}
                  <div className="bg-white h-full flex flex-col rounded-xl shadow-lg">
                    {/* A div da imagem tem os cantos arredondados e o overflow-hidden para conter o zoom */}
                    <div className="relative h-56 rounded-t-xl overflow-hidden">
                      <Image 
                        fill
                        sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                        src={localImages[index % localImages.length]}
                        alt={`Viagem para ${pkg.destino}`}
                        className="object-cover group-hover:scale-110 transition-transform duration-500"
                        placeholder="blur"
                      />
                      <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
                      <div className="absolute bottom-4 left-4">
                        <h3 className="text-2xl font-bold text-white">{pkg.destino}</h3>
                        <p className="text-sm text-gray-200">Saindo de {pkg.origem}</p>
                      </div>
                    </div>
                    <div className="p-6 flex flex-col flex-grow">
                      <p className="text-gray-600 mb-4 line-clamp-3 flex-grow">{pkg.descricao || `Descubra as maravilhas de ${pkg.destino} com nosso pacote exclusivo.`}</p>
                      <div className="flex justify-between items-end gap-4 mt-4">
                        <div>
                          <p className="text-sm text-gray-500">A partir de</p>
                          <p className="text-2xl font-bold text-green-600">
                            {new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(pkg.preco)}
                          </p>
                        </div>
                        <div className="py-2 px-5 bg-green-500 text-white font-semibold rounded-lg shadow-md group-hover:bg-green-600 transition-colors whitespace-nowrap">
                          Ver Detalhes
                        </div>
                      </div>
                      <p className="text-xs text-gray-400 mt-4 text-right">Data: {formattedDate}</p>
                    </div>
                  </div>
                </Link>
              );
            })}
          </div>
        )}
      </main>
    </div>
  );
}
